* {
  box-sizing: border-box;
}

.is-hidden {
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect (0 0 0 0);
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.photo-card {
  max-width: 100%;
  border: 1px solid #000;
  display: block;
}

.search-form {
  max-width: 400px;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  display: flex;
}

.search-form input[type="text"] {
  border: 1px solid #ccc;
  border-radius: 4px;
  flex: 1;
  padding: 10px;
  font-size: 16px;
}

.search-form button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  margin-left: 10px;
  padding: 10px 20px;
  font-size: 16px;
  transition: background-color .3s;
}

.search-form button:hover {
  background-color: #0056b3;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 25px;
  margin-top: 100px;
  display: flex;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  margin: 20px auto;
  padding: 10px 20px;
  font-size: 16px;
  transition: background-color .3s;
  display: block;
}

.load-more:hover {
  background-color: #0056b3;
}

.load-more.is-hidden {
  display: none;
}

.photo-card {
  text-align: center;
  flex-direction: column;
  align-items: center;
  transition: box-shadow .3s;
  display: flex;
}

.photo-card:hover {
  box-shadow: 0 0 10px rgba(0, 0, 0, .2);
}

.photo-card img {
  width: 900px;
  height: auto;
  margin-bottom: 10px;
}

.photo-card img:hover {
  border: 2px solid #007bff;
}

.info {
  width: 100%;
  justify-content: space-around;
  display: flex;
}

.info p {
  margin: 0;
  transition: color .3s;
}

.info p:hover {
  color: #007bff;
}

.info-item {
  flex: 1;
  padding: 25px;
  font-family: sans-serif;
}

