
*{
    box-sizing: border-box; 
}
 
    
    .is-hidden{
        position: absolute; 
        overflow: hidden; 
        margin: -1px; 
        padding: 0; 
        white-space: 
        nowrap; 
        clip-path: inset(100%); clip: rect (0 0 0 0);
    }
        
    .photo-card {
        display: block;
        max-width: 100%;
        border: 1px solid black; 
    }
 
.search-form {
    justify-content: center;
    display: flex;
    align-items: center;
    max-width: 400px;
    margin: 0 auto;
  }
  

  .search-form input[type="text"] {
    flex: 1;
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 4px;
  }

  .search-form button {
    padding: 10px 20px;
    font-size: 16px;
    border: none;
    border-radius: 4px;
    background-color: #007bff;
    color: #fff;
    margin-left: 10px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
 
  .search-form button:hover {
    background-color: #0056b3;
  }
     .gallery{
        display: flex;
        flex-wrap: wrap;
        margin-top: 100px;
        justify-content: center;
        gap: 25px;
     }


     .load-more {
        display: block;
        margin: 20px auto; 
        padding: 10px 20px;
        font-size: 16px;
        border: none;
        border-radius: 4px;
        background-color: #007bff;
        color: #fff;
        cursor: pointer;
        transition: background-color 0.3s ease;
      }
      
      .load-more:hover {
        background-color: #0056b3;
      }
      
      
      .load-more.is-hidden {
        display: none;
      }

      .photo-card {
        display: flex;
        flex-direction: column;
        align-items: center; 
        text-align: center; 
        transition: box-shadow 0.3s ease;
      }
      .photo-card:hover {
        box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
        
      }

      
      .photo-card img {
        width: 900px; 
        height: auto; 
        margin-bottom: 10px; 
      }

      .photo-card img:hover {
        border: 2px solid #007bff; 
      }

      .info {
        display: flex;
        justify-content: space-around;
        width: 100%;
      }
      
      
        .info p {
            margin: 0;
            transition: color 0.3s ease; 
          }

          .info p:hover {
            color: #007bff; 
          }
      
      .info-item {
     
        font-family:  sans-serif;
        flex: 1;
        padding: 25px 25px; 
      }